@import url('https://fonts.googleapis.com/css2?family=Lumanosimo&display=swap');
@import url('https://fonts.googleapis.com/css2?family=GFS+Didot&display=swap');

.quiz-container {
  background: url('../../images/pic6.png');
  background-size: 100% 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 90vh;
}

.container {
  display: flex;
  align-items: center;
  flex-direction: column;
  min-height: 65vh;
  width: 30%;
  background-color: white;
  border-radius: 15px;
  box-shadow: 2px 2px 10px 10px #dcdbdb;
  overflow: hidden;
  padding: 2%;
  position: relative;
  background-color: aliceblue;
}

.question {
  text-align: left;
  margin: 1%;
  border-radius: 5px;
  padding: 2%;
  font-size: 1.3rem;
  font-family: 'GFS Didot', serif;
  font-weight: 800;
  min-width: 100%;
  min-height: 10vh;
}


.option-container {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 2% 5%;
}

.option-btn {
  box-shadow: 0px 0px 4px 1px rgb(190, 185, 185);
  padding: 2%;
  border-radius: 5px;
  border: none;
  outline: none;
  transition: 0.3s;
  margin: 2%;
  min-height: 5vh;
  background-color: rgb(120, 149, 175);
  font-family: 'GFS Didot', serif;
  font-weight: 600;
  color: white;
  letter-spacing: 0.1rem;
  font-size: 1.1rem;
  cursor: pointer;
}

.correct{
  background-color: green;
}
.incorrect{
  background-color: red;
}

.checked {
  background-color: rgb(83, 177, 179);
  color: white;
  box-shadow: 3px 3px 4px 1px rgb(78, 69, 78);
}

.option-btn:hover {
  background-color: rgb(102, 128, 150);
  color: white;
}

#next-button {
  cursor: pointer;
  padding: 2%;
  border: none;
  background-color: #0C2D48;
  color: white;
  border-radius: 5px;
  box-shadow: 3px 3px 2px 1px rgb(192, 191, 191);
  font-size: 1.2rem;
  font-family: 'GFS Didot', serif;
  font-weight: 600;
  letter-spacing: 0.1rem;
  transition: 0.2s;
  width: 25vh;
  position: absolute;
  bottom: 6%;
}

#next-button:active {
  box-shadow: none;
}

#next-button:hover{
  background-color: #124168;
}

.show-score {
  position: absolute;
  top: 10%;
  font-size: 3rem;
  text-align: center;
  font-family: 'Lumanosimo', cursive;
  font-family: 'GFS Didot', serif;
  font-weight: 1000;
  padding: 5%;
}

.score{
  margin: 10% auto;
}

@media only screen and (max-width: 1200px) {
  .container {
    width: 40%;
  }
  .option-container{
    padding: 2% 4%;
  }
  .option-btn{
    min-height: 3vh;
    font-size: 1rem;
    margin: 1.4%;
  }
}
@media only screen and (max-width: 650px) {
  .container {
    width: 80%;
  }
  .question {
    padding: 1%;
  }
  .option-container{
    padding: 2%;
  }
  .option-btn{
    min-height: 2vh;
    font-size: 1rem;
    margin: 1.2%;
  }
  #next-button{
  padding: 1.5%;
    
  }
}
@media only screen and (max-width: 350px) {
  .container {
    width: 100%;
  }
  .question {
    padding: 1%;
  }
  .option-container{
    padding: 2%;
  }
  .option-btn{
    min-height: 2vh;
    font-size: 1rem;
    margin: 1.1%;
  }
  #next-button{
    padding: 1.5%;
  }
}